.form-checkbox {
  position: relative; }

.form-checkbox input[type="checkbox"] + .checkbox-indicator {
  position: absolute;
  top: 5px;
  left: -21%;
  width: 16px;
  height: 16px;
  border: 1px solid white; }

.form-checkbox input[type="checkbox"]:checked + .checkbox-indicator::before {
  content: "";
  position: absolute;
  top: 7px;
  left: 2px;
  width: 4px;
  height: 2px;
  background-color: white;
  transform: rotate(45deg); }

.form-checkbox input[type="checkbox"]:checked + .checkbox-indicator::after {
  content: "";
  position: absolute;
  top: 6px;
  left: 4px;
  width: 8px;
  height: 2px;
  background-color: white;
  transform: rotate(-45deg); }

